import {useEffect, useState} from "react";
import './App.css';
import 'antd/dist/antd.css';
import {
  BACKGROUND_COLOR, CHAIN_ID,
  CONTRACT_ADDRESS,
  DROPS_ABI,
  PRICE, START_TIME,
  TOTAL_NFT_COUNT
} from "constants/Values";
import caver, {
  getChainId,
  hasKaikas,
  networkChanged,
  selectedAddress
} from "util/caver";
import {Modal, Row} from "antd";
import styles from './app.module.scss';
import ConnectWalletButton from "./components/ConnectWalletButton";
import MintCountButton from "./components/MintCountButton";
import MintingButton from "./components/MintingButton";

function App() {
  const [address, setAddress] = useState(null);
  const [mintCount, setMintCount] = useState(1);
  const [chainId, setChainId] = useState(1);
  const [leftCount, setLeftCount] = useState(TOTAL_NFT_COUNT);

  useEffect(() => {
    setChainId(getChainId());

    networkChanged((chainId) => {
      setChainId(chainId);
    });
  }, []);

  useEffect(async () => {
    try {
      const contractAbi = new caver.klay.Contract(DROPS_ABI, CONTRACT_ADDRESS);
      const count = await contractAbi.call('getLeftDropCount');

      setLeftCount(count);
    } catch (e) {}
  }, []);

  useEffect(() => {
    if (chainId > 1 && chainId != CHAIN_ID) {
      return Modal.error({
        title: '네트워크 에러',
        content: '메인넷으로 변경해주세요.',
      });
    }
  }, [chainId]);

  useEffect(async () => {
    if (!hasKaikas()) {
      return Modal.error({
        title: '에러',
        content: '카이카스 지갑이 필요합니다.',
      });
    }
    try {
      const address = await selectedAddress();
      setAddress(address);
    } catch (e) {}
  }, []);

  // useEffect(async () => {
  //   try {
  //     const contractAbi = new caver.klay.Contract(DROPS_ABI, CONTRACT_ADDRESS);
  //     const count = await contractAbi.call('getLeftDropCount');
  //
  //     setLeftCount(count);
  //   } catch (e) {}
  // }, []);

  const connectWallet = async () => {
    if (address) {
      return Modal.confirm({
        title: '지갑 연결 해제',
        content: '지갑 연결을 해제하시겠습니까?',
        okText: '해제하기',
        cancelText: '취소',
        onOk: () => setAddress(null),
      });
    }
    try {
      const account = await selectedAddress();
      setAddress(account);
    } catch (e) {}
  }

  const mint = async () => {
    if (chainId > 1 && chainId != CHAIN_ID) {
      return Modal.error({
        title: '네트워크 에러',
        content: '메인넷으로 변경해주세요.',
      });
    }
    if (!address) {
      return Modal.error({
        title: '에러',
        content: '지갑을 연결해주세요.',
      });
    }

    const contractAbi = new caver.klay.Contract(DROPS_ABI, CONTRACT_ADDRESS);
    const amount = PRICE < 1 ? (parseFloat(PRICE) * mintCount).toFixed(6) : PRICE * mintCount;

    try {
      const receipt = await contractAbi.methods.drops(mintCount)
        .send({
          from: address,
          value: caver.utils.toPeb(amount, 'KLAY'),
          gas: '30000000',
        });

      if (receipt) {
        return Modal.info({
          title: '구매 성공',
          content: <>구매 성공. 클레이튼 스코프를 확인해주세요. <a href={`https://v2.scope.klaytn.com/account/${address}?tabId=txList`}>이동하기</a></>,
        });
      } else {
        return Modal.error({
          title: '일시적인 에러',
          content: <>민팅 결과를 확인할 수 없습니다. 클레이튼 스코프를 확인해주세요. <a href={`https://v2.scope.klaytn.com/account/${address}?tabId=txList`}>이동하기</a></>,
        });
      }
    } catch (e) {
      if (e.message === 'Returned error: Error: Kaikas Tx Signature: User denied transaction signature.') {
        return Modal.error({
          title: '취소',
          content: '취소되었습니다.',
        });
      } else {
        return Modal.error({
          title: '일시적인 에러',
          content: <>민팅 결과를 확인할 수 없습니다. 클레이튼 스코프를 확인해주세요. <a href={`https://v2.scope.klaytn.com/account/${address}?tabId=txList`}>이동하기</a></>,
        });
      }
    }
  }

  return (
    <div style={{background: BACKGROUND_COLOR, width: '100%', height: '1024px'}}>
      <div className={styles.background_container}>
        <Row style={{width: '100%', height: '100%'}}>
          <div style={{width: '784px', height: '940px', marginTop: '45px', marginLeft: '631px'}}>
            <div className={styles.mint_container}>
              <div style={{paddingTop: '58px'}}>
                <img src="./images/title.png" style={{marginLeft: '40px', marginTop: '-50px'}} /><img src="./images/btn_info.png" className={styles.btn_info} onClick={() => window.open("https://medium.com/@BotGuevara/%EB%AF%BC%ED%8C%85%EC%95%88%EB%82%B4-6f0732dbffc", "_blank")} />
              </div>
              <div>
                <span className={styles.start_date_text}>{START_TIME}</span>
                <span className={styles.minting_count_text}>{leftCount}/{TOTAL_NFT_COUNT}</span>
              </div>
              <ConnectWalletButton address={address} onClick={connectWallet} />
              <div style={{marginLeft: '146px', marginTop: '130px'}}>
                <img src="./images/mint_count.png" style={{float: 'left'}} />
                <div style={{marginLeft: '32px', float: 'left'}}>
                  <MintCountButton count={1} selected={mintCount === 1} setMintCount={setMintCount} />
                  <MintCountButton count={3} selected={mintCount === 3} setMintCount={setMintCount} />
                  <MintCountButton count={5} selected={mintCount === 5} setMintCount={setMintCount} />
                </div>
              </div>
              <div>
                <MintingButton onClick={mint} price={PRICE * mintCount} />
              </div>
            </div>
          </div>
        </Row>
      </div>
    </div>
  );
}

export default App;
