import styles from './MintingButton.module.scss';

const MintingButton = ({price, onClick}) => {
  return (
      <div className={styles.minting_button_button_back} onClick={onClick}>
        <span className={styles.minting_button_text}>민팅시작</span>
        {
          price < 100 && <span className={styles.minting_button_price_text}>{price} Klay</span>
        }
        {
          price >= 100 && price < 200 && <span className={styles.minting_button_price_text3}>{price} Klay</span>
        }
        {
          price >= 200 && <span className={styles.minting_button_price_text5}>{price} Klay</span>
        }
      </div>
  )
}

export default MintingButton;