import styles from './MintCountButton.module.scss';

const MintCountButton = ({count, selected,  setMintCount}) => {
  return (
      <div className={selected ? styles.mint_count_button_selected : styles.mint_count_button_unselected} onClick={() => setMintCount(count)}>
        <span className={selected ? styles.mint_count_button_selected_text : styles.mint_count_button_unselected_text}>{count}</span>
      </div>
  )
}

export default MintCountButton;