import Caver from 'caver-js';

const {klaytn} = window;

const caver = new Caver(klaytn);

export default caver;

export const hasKaikas = () => {
  return klaytn;
};

export const selectedAddress = async () => {
  if (klaytn) {
    const accounts = await klaytn.enable();

    return accounts[0];
  }
};

export const getChainId = () => {
  return klaytn?.networkVersion;
}

export const accountsChanged = callback => {
  klaytn?.on('accountsChanged', accounts => {
    if (callback) {
      callback(accounts[0]);
    }
  });
};

export const networkChanged = callback => {
  klaytn?.on('networkChanged', () => {
    if (callback) {
      callback(klaytn.networkVersion);
    }
  });
};