import styles from './ConnectWalletButton.module.scss';

const ConnectWalletButton = ({address, onClick}) => {
  return (
      <div className={styles.connect_wallet_button_back} onClick={onClick}>
        <div className={styles.connect_wallet_button_icon} />
        <span className={address ? styles.connect_wallet_button_unlock_text : styles.connect_wallet_button_lock_text}>
          {address ? `${address.substring(0, 6)}...${address.substring(address.length - 6)}` : '지갑연결'}
        </span>
      </div>
  )
}

export default ConnectWalletButton;